import React from 'react';
import classes from './StudioTile.scss'
import Divider from 'material-ui/Divider';
import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import FlatButton from 'material-ui/FlatButton'
import {Link} from 'react-router'

const styles = {
  card: {
    margin: '30px',
    cursor: 'pointer',
    maxWidth: '350px',
    display: 'inline-block'
  },
  cardMediaContainer : {
    overflow : "hidden",
    minHeight: '350px',
    maxHeight: '350px'
  }
};
const StudioTile = (props) => (
  <div>
    {props.list.map((tile,index) => (
      <Link key={index} to={`/studio/${index}`}>
        <Card
          style={styles.card}
          className={classes.cardContainer}
          >
          <CardMedia
            style={styles.cardMediaContainer}
          >
            <img src={tile.img} className={classes.imghover}/>
          </CardMedia>
          <CardTitle title={tile.title} subtitle={tile.author} />
          <Divider/>
          <CardHeader
            title={tile.title}
            avatar="jsa-128.jpg"
          />
        </Card>
      </Link>
    ))}
  </div>
);


StudioTile.propTypes = {
  list : React.PropTypes.array.isRequired
}

export default StudioTile;



